@use "global" as *;

.p-footer {
  padding: rem(32) rem(32) rem(60);
}
.p-footer__inner {
  display: flex;
  align-items: center;
}
.p-footer__logo {
  width: rem(70);
  
}
.p-footer__body {
  margin-left: auto;
}
.p-footer__items {
  display: flex;
  align-items: center;
 
}
.p-footer__item {
  font-size: rem(16);
  position: relative;
  color: $white;
  text-align: center;
}
.p-footer__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-top: rem(5);
  background: $yellow;
  opacity: 0;
  transition: .3s;
}
.p-footer__item:hover::after {
  opacity: 1;
}
.p-footer__item + .p-footer__item {
  margin-top: initial;
  margin-left: rem(32);
}
.p-footer__copy {
  font-size: rem(11);
    color: $white;
    text-align: center;
    padding-bottom: rem(60);
}

@include mq(md) {
  .p-footer {
    padding: rem(58) 0 rem(32);
  }
  .p-footer__inner {
     display: block;
  }
  .p-footer__logo {
    width: rem(70);
    margin: auto;
  }
  .p-footer__logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .p-footer__body {
    margin-top: rem(8);
  }
  .p-footer__items {
    display: block;
  }
  .p-footer__item {
  
    
  }
  .p-footer__item + .p-footer__item {
    margin-top: rem(8);
    margin-left: initial;
  }
  .p-footer__copy {
    font-size: rem(11);
  }
}